import React, { useState } from "react";
import Cart from "../Carts-section/Cart";
import "./Feature-section.css";

const Feature_Section = () => {
  const [featuredProducts, setFeaturedProducts] = useState([
    {
      id: 1,
      image: "/Porto/images/card-item1.jpg",
      name: "Running shoes",
      price: "$99",
      rating_count: "(42.5k)",
      rating:4
    },
    {
      id: 2,
      image: "/Porto/images/card-item2.jpg",
      name: "Sports shoes  ",
      price: "$89",
      rating_count: "(62.6k)",
      rating:4
    },
    {
      id: 3,
      image: "/Porto/images/card-item3.jpg",
      name: "Casual shoes  ",
      price: "$79",
      rating_count: "(11.55k)",
      rating:4
    },
    {
      id: 4,
      image: "/Porto/images/card-item7.jpg",
      name: "Formal shoes  ",
      price: "$69",
      rating_count: "(30.38k)",
      rating:4
    },
    {
      id: 5,
      image: "/Porto/images/card-item5.jpg",
      name: "Sneakers  ",
      price: "$59",
      rating_count: "(23.42k)",
      rating:4
    },
    {
      id: 6,
      image: "/Porto/images/card-item6.jpg",
      name: "Hiking boots  ",
      price: "$109",
      rating_count: "(31.33k)",
      rating:4
    },
    {
      id: 7,
      image: "/Porto/images/card-item7.jpg",
      name: "Leather sandals  ",
      price: "$49",
      rating_count: "(20.91k)",
      rating:4
    },
    {
      id: 8,
      image: "/Porto/images/card-item8.jpg",
      name: "Trail running shoes  ",
      price: "$119",
      rating_count: "(44.57k)",
      rating:4
    },
    {
      id: 9,
      image: "/Porto/images/card-image1.jpg",
      name: "Loafers  ",
      price: "$79",
      rating_count: "(64.79k)",
      rating:4
    },
    {
      id: 10,
      image: "/Porto/images/category_img_02.jpg",
      name: "Slip-on shoes  ",
      price: "$69",
      rating_count: "(21.32k)",
      rating:4
    },

  ]);

  const handleBookmarkToggle = (id) => {
    setFeaturedProducts((prevProducts) =>
      prevProducts.map((product) =>
        product.id === id
          ? { ...product, isBookmarkSolid: !product.isBookmarkSolid }
          : product
      )
    );
  };

  const handleRatingChange = (id, value) => {
    setFeaturedProducts((prevProducts) =>
      prevProducts.map((product) =>
        product.id === id ? { ...product, rating: value } : product
      )
    );
  };

  return (
    <>
      <div className="feature_section">
        <div className="display-header d-flex align-items-center justify-content-between">
          <h2 className="section-title text-uppercase">Featured Products</h2>
          <div className="btn-right">
            <a
              href="index.html"
              className="d-inline-block text-uppercase text-hover fw-bold"
            >
              View all
            </a>
          </div>
        </div>
        <Cart
          products={featuredProducts}
          onBookmarkToggle={handleBookmarkToggle}
          onRatingChange={handleRatingChange}
        />

        
      </div>
    </>
  );
};

export default Feature_Section;
