import React, { useState } from "react";
import "./Header.css";

const Header = () => {
  const [isVisible, setIsVisible] = useState(true);

  const handleClose = () => {
    setIsVisible(false); // Hide the promo section
  };

  return (
    <>
      {isVisible && (
        <div className="promo_section">
          <div className="container">
            <div className="promo_section_inner">
              <i className="fa-solid fa-rectangle-ad"></i>
              
              <div className="promo_section_sub_heading">
                <p>
                  Get Up to <span>40% OFF</span> New-Season Styles
                </p>
              </div>
              <div className="promo_section_men_women">
                <a href="#">MEN</a>
                <a href="#">WOMEN</a>
              </div>
              <div className="promo_section_condition">
                <p>* Limited time only.</p>
              </div>
              {/* Close button */}
              <i
                className="fa-solid fa-xmark"
                id="closePromo"
                onClick={handleClose}
              ></i>
            </div>
          </div>
        </div>
      )}
      <div className="main_header">
        {/* <div className="container"> */}
          <div className="main_header_inner">
            <div className="header_top">
              <div className="header_top_inner">
                <div className="top_header_right">
                  <div className="announcement_bar">
                    <div className="announcement_bar_inner">
                      <div className="bar_message">
                        <p>
                          Express delivery and free returns within 30 days
                          <span>
                            <a href="/"> See details</a>
                          </span>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="top_header_left">
                  <div className="top_menu">
                    <div className="top_menu_inner">
                      <ul>
                        <li>
                          <a href="/">About Us</a>
                        </li>
                        <li>
                          <a href="/">Order Tracking</a>
                        </li>
                        <li>
                          <a href="/">Contact Us</a>
                        </li>
                        <li>
                          <a href="/">FAQs</a>
                        </li>
                      </ul>
                    </div>
                  </div>
                  <div className="language_curreny_menu">
                    <div className="language_menu">
                      <div className="language_menu_inner parent_list">
                        <div className="language_select">
                          <p>
                            English <i className="fa-solid fa-angle-down"></i>
                          </p>
                        </div>
                      </div>
                    </div>
                    <div className="country_currency">
                      <div className="country_currency_inner">
                        <div className="currency_select">
                          <p>
                            United States (USD $)
                            <i className="fa-solid fa-angle-down"></i>
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="header_middle">
              <div className="header_middle_inner">
                <div className="site_logo">
                  <div className="site_logo_inner">
                    {/* <img src="/Porto/images/Shopping_logo_white(1).png" alt="/" /> */}
                    {/* <img src="/Porto/images/Morata-logo.png" alt="/" /> */}
                    <img src="/Porto/images/logo_ecomwhite1.png" alt="/" />
                  </div>
                </div>

                <div className="product_search_bar">
                  <div className="product_search_bar_inner">
                    <div className="search_bar">
                      <div className="search_bar_inner">
                        <div className="all_category">
                          <div className="all_category_inner">
                            All Categories
                            <span>
                              <i className="fa-solid fa-angle-down"></i>
                            </span>
                          </div>
                        </div>
                        <div className="search_bar_holder">
                          <div className="search_bar_holder_inner">
                            <input
                              type="text"
                              id="search-bar"
                              placeholder="Search for products"
                            />
                          </div>
                        </div>
                        <button className="search-button">Search</button>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="user_utilities">
                  <div className="user_utilities_inner">
                    <a className="user_profile" href="/">
                      <div className="user_icon">
                        <i className="fa-regular fa-user"></i>
                      </div>
                      <div className="user_utilities_text">
                        <div className="user_utilities_text_upper">
                          <span>Login</span>
                        </div>
                        <div className="user_utilities_text_bottom">
                          <span>Account</span>
                        </div>
                      </div>
                    </a>
                  </div>
                </div>

                <a className="user_profile" href="/">
                  <div className="user_icon">
                    <div className="user_utilities_heart_counter">
                      <span>0</span>
                    </div>
                    <i className="fa-regular fa-heart"></i>
                  </div>
                  <div className="user_utilities_text">
                    <div className="user_utilities_text_upper">
                      <span>Favorite</span>
                    </div>
                    <div className="user_utilities_text_bottom">
                      <span>My Wishlist</span>
                    </div>
                  </div>
                </a>

                <a className="user_profile" href="/">
                  <div className="user_icon">
                    <div className="user_utilities_cart_counter">
                      <span>0</span>
                    </div>
                    <i className="fa-solid fa-cart-shopping"></i>
                  </div>
                  <div className="user_utilities_text">
                    <div className="user_utilities_text_upper">
                      <span>Your Cart</span>
                    </div>
                    <div className="user_utilities_text_bottom">
                      <span>$0.00</span>
                    </div>
                  </div>
                </a>
              </div>
            </div>
            <div className="header_bottom">
              <div className="header_bottom_inner">
                <div className="header_bottom_right_menu">
                  <div className="header_bottom_right_menu_inner">
                    <div className="shop_by_department">
                      <div className="shop_by_department_inner">
                        <span>
                          <i className="fa-solid fa-list"></i>Shop by category
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="header_bottom_middle_menu">
                  <ul className="header_bottom_middle_menu_inner">
                    <li>
                      <a href="/">HOME</a>
                      <i className="fa-solid fa-angle-down"></i>
                    </li>
                    <li>
                      <a href="/">SHOP</a>
                      <i className="fa-solid fa-angle-down"></i>
                    </li>
                    <li>
                      <a
                        className="header_bottom_middle_menu_icon_item"
                        href="/"
                      >
                        <img src="/Porto/images/new-icon.png" alt="/" />
                        PRODUCTS
                      </a>
                      <i className="fa-solid fa-angle-down"></i>
                    </li>
                    <li>
                      <a
                        className="header_bottom_middle_menu_icon_item"
                        href="/"
                      >
                        <img src="/Porto/images/hot-icon.png" alt="/" />
                        DEALS
                      </a>
                      <i className="fa-solid fa-angle-down"></i>
                    </li>
                    <li>
                      <a href="/">BLOG</a>
                      <i className="fa-solid fa-angle-down"></i>
                    </li>
                    <li>
                      <a href="/">CONTACT US</a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        {/* </div> */}
      </div>
    </>
  );
};

export default Header;
