import React, { useState, useEffect } from "react";
import { Rate } from "antd";
import "./Cart_1.css";

const Cart_1 = () => {
  const initialProducts = [
    {
      id: 1,
      image: "/Porto/images/products-3-1.jpg",
      name: "Apple iPad Air 4 10 .9-inch Wi-Fi 256GB",
      price: "$1500",
      isBookmarkSolid: false,
      rating: 2,
    },
    {
      id: 2,
      image: "/Porto/images/products_4_1.jpg",
      name: "Apple iPad Mini 6 Cellular 64GB/128GB",
      price: "$1500",
      isBookmarkSolid: false,
      rating: 2,
    },
    {
      id: 3,
      image: "/Porto/images/products_2_1.jpg",
      name: "Apple iPad Pro M1 11-inch 2021 Wi-Fi 128GB",
      price: "$1500",
      isBookmarkSolid: false,
      rating: 2,
    },
    {
      id: 4,
      image: "/Porto/images/products_9_1.jpg",
      name: "Apple iPhone 13 Mini 128GB Pink Unlocked",
      price: "$999",
      isBookmarkSolid: false,
      rating: 2,
    },
    {
      id: 5,
      image: "/Porto/images/products_34_1.jpg",
      name: "Apple iPhone 12 Pro Max 128GB - Unlocked",
      price: "$899",
      isBookmarkSolid: false,
      rating: 2,
    },
  ];

  const [products, setProducts] = useState(() => {
    const savedProducts = localStorage.getItem("bookmarkedProducts");
    return savedProducts ? JSON.parse(savedProducts) : initialProducts;
  });

  useEffect(() => {
    localStorage.setItem("bookmarkedProducts", JSON.stringify(products));
  }, [products]);

  const handleBookmarkColor = (id) => {
    setProducts((prevProducts) =>
      prevProducts.map((product) =>
        product.id === id
          ? { ...product, isBookmarkSolid: !product.isBookmarkSolid }
          : product
      )
    );
  };

  const handleRatingChange = (id, value) => {
    setProducts((prevProducts) =>
      prevProducts.map((product) =>
        product.id === id ? { ...product, rating: value } : product
      )
    );
  };

  return (
    <div className="cart_1">
      <div className="cart_inner">
        {products.map((product) => (
          <div className="cart1_item" key={product.id}>
            <div className="cart1_item_inner">
              <img src={product.image}  />
              <div className="cart1_item_rating"></div>
              <p className="cart1_item_tag">Limited time deal</p>
              <a href="#">{product.name}</a>

              <div className="cart1_item_product_price_wrapper">
                <span className="cart1_item_product_price">
                  {product.price}{" "}
                  <span>
                    M.R.P: <span className="cart1_item_discount">$1699 </span>
                    (29% Off) <span className="kohli"></span>
                  </span>
                </span>
                <span>
                  <Rate
                    allowHalf
                    value={product.rating}
                    onChange={(value) => handleRatingChange(product.id, value)}
                  />
                </span>
              </div>

              <div className="cart1_item_actions">
                <a href="#" className="cart1_item_buy_now">
                  Buy Now
                </a>
                <span>
                  <i className="fa-solid fa-right-left"></i>
                </span>
                <span>
                  <i
                    className={
                      product.isBookmarkSolid
                        ? "fa-solid fa-bookmark"
                        : "fa-regular fa-bookmark"
                    }
                    onClick={() => handleBookmarkColor(product.id)}
                  ></i>
                </span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Cart_1;
